	data() {
		return {
			selecdPayment: [
				{
					type: true
				},
				{
					type: false
				},
				{
					type: false
				}
			]
		};
	},
	methods: {
		optionOfPayment(idx) {
			this.selecdPayment.forEach((v, i) => {
				v.type = false;

				if (i == idx) {
					v.type = true;
				}
			});
		}
	}
};
